
.Like {
    stroke: var(--default-color-02);
    transition: 0.3s;
    -webkit-transition: 0.3s;
    -moz-transition: 0.3s;
    -ms-transition: 0.3s;
    -o-transition: 0.3s;
    &:hover{
      stroke:var(--accent);
    }
  }
  
  .Exit, .Bell {
    fill:var(--default-color-02);
    transition: 0.3s;
    -webkit-transition: 0.3s;
    -moz-transition: 0.3s;
    -ms-transition: 0.3s;
    -o-transition: 0.3s;
    &:hover{
      fill: var(--accent);
    }
  }
  .More {
    color: var(--default-color-02);
    transition: 0.3s;
    -webkit-transition: 0.3s;
    -moz-transition: 0.3s;
    -ms-transition: 0.3s;
    -o-transition: 0.3s;
    &:hover{
      color: var(--accent);
    }
  }
  
  .More , .Like, .Exit, .Bell{
    cursor: pointer;
  }

  .MoreActiv > .More {
    color: var(--accent);
  }
  .centredExit{
    width: 24px;
    height: 24px;
    display: flex;
    justify-items: center;
    align-items: center;
  }
  .Loader{
    width: 40px;
    height: 200px;
  }

  .CentreLoader{
    position: fixed;
    top: 25%;
    width: 350px;
    height: 350px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
