#root {
  border: 0;
}
body{
  background-color: var(--default-color-05);
  margin: 0;
  width: 100%;
  min-width: 375px;
  display: flex;
  justify-content: center;
}

html {
  font-family: 'Manrope', sans-serif;
}

body::-webkit-scrollbar {
  width: 12px;
  }
  
body::-webkit-scrollbar-track {
  background:var(--default-color-05); 
  }
  
body::-webkit-scrollbar-thumb {
  background-color:var(--default-color-04);
  border-radius: 5em;
  border: 2px solid #050c26;
  }

